export default {
	data() {
		return {
			"instruments": [],
			"InstrumentCode": "",
			"userName": "",
			"userPhone": "",
			"DamageInformation": ""
		}
	},
	methods: {
		submit: function() {

			if (this.userName === "" || this.userPhone == "") {
				uni.showModal({
					title: "提示",
					content: "请输入您的姓名和联系电话",
					showCancel: false

				})
			}


		},

		changeInstrumentCode: function(e) {
			console.log(e.detail.value)



			this.InstrumentCode = e.detail.value

		},


		add: function() {





			// debugger
			var instrument = this.instruments[this.instruments.length - 1]
			console.log(instrument)
			/* 先判断列表中数据是否完整 */

			if (this.instruments.length === 0) {
				this.instruments.push({
					ID: 0,
					InstrumentCode: "",
					InstrumentName: "",
					FacilitiesName: "",
					DamageInformation: ""
				})

			} else {

				if (!instrument.InstrumentCode || !instrument.DamageInformation) {

					uni.showToast({
						title: "请先完成上个器材",
						icon: "none"
					})


				} else {

					this.instruments.push({
						ID: 0,
						InstrumentCode: "",
						InstrumentName: "",
						FacilitiesName: ""
					})

				}
			}





			/* {ID: 5419, InstrumentCode: "08M08MJYZJW060106", InstrumentName: "太极云手", FacilitiesName: "南方城健身苑点A"} */



		},
		requestInstrumentInfo: function(index) {


			console.log("下标：" + index)


			/* 先判断编号是否为空 */

			console.log(this.InstrumentCode)


			uni.showLoading({
				title: "正在获取器材"
			})

			this.$api.getInfoByNumber({
				"InstrumentCode": this.InstrumentCode
			}).then((res) => {

				/* 
				{ID: 5419, InstrumentCode: "08M08MJYZJW060106", InstrumentName: "太极云手", FacilitiesName: "南方城健身苑点A"}
				 */
				console.log(res)

				this.instruments[index] = res

				this.$set(this.instruments, this.instruments[index], res)

				uni.hideLoading()

			}).catch((err) => {
				uni.hideLoading()

				uni.showToast({
					title: err.Message,
					icon: "none",
					mask: true
				})

				console.log('request fail', err);
			})
		}
	}
}
